/* Overall page background and typography */
body {
    background: linear-gradient(to bottom right, #1f1c2c, #928dab);
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    margin: 0;
    padding: 0;
    color: #fff;
}

/* Main app container */
.App {
    text-align: center;
    padding: 2rem;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Main headings */
h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
    text-shadow: 2px 2px 8px rgba(0, 0, 0, 0.6);
}

h2 {
    margin-top: 2rem;
    font-size: 2rem;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

/* --- Attack List Styles --- */
.attack-list-container {
    background: rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    padding: 1.5rem;
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    margin-bottom: 2rem;
}

.attack-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 1rem;
    list-style: none;
    padding: 0;
}

/* --- Attack Item Styles --- */
.attack-item {
    background: rgba(255, 255, 255, 0.2);
    border: 1px solid rgba(255, 255, 255, 0.3);
    border-radius: 16px;
    padding: 1rem;
    transition: transform 0.2s ease, box-shadow 0.2s ease;
    cursor: pointer;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.attack-item:hover {
    transform: translateY(-5px);
    box-shadow: 0 12px 20px rgba(0, 0, 0, 0.5);
}

.attack-text-container h4 {
    margin: 0 0 0.5rem 0;
    font-size: 1.1em;
}

.attack-text-container p {
    margin: 0.2rem 0;
}

/* --- Attack Images --- */
.attack-images {
    margin-top: 0.5rem;
}

.attack-img {
    width: 50px;
    height: 50px;
    object-fit: contain;
    margin: 0 5px;
}

/* --- Search and Filter Controls --- */
.controls-area {
    background: rgba(0, 150, 255, 0.2);
    border-radius: 16px;
    padding: 1rem;
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    margin-bottom: 2rem;
}

.controls-area input[type="text"],
.controls-area select {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 8px;
    font-size: 1rem;
    outline: none;
}

/* --- Attack Detail Styles --- */
.attack-detail-container {
    background: rgba(0, 0, 0, 0.5);
    border-radius: 16px;
    padding: 2rem;
    box-shadow: 0 8px 20px rgba(0,0,0,0.6);
    max-width: 600px;
    margin: 2rem auto;
    text-align: left;
}

.back-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 8px;
    background: linear-gradient(45deg, #ff416c, #ff4b2b);
    color: #fff;
    font-weight: bold;
    cursor: pointer;
    transition: background 0.3s ease;
    margin-bottom: 1rem;
}

.back-button:hover {
    background: linear-gradient(45deg, #ff4b2b, #ff416c);
}

.attack-title {
    font-size: 1.8em;
    margin-bottom: 1rem;
    color: #ffe600;
    text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.5);
}

.attack-img {
    max-width: 100%;
    height: auto;
    display: block;
    margin-bottom: 1rem;
    border-radius: 8px;
    border: 1px solid #eee;
}

.saga-text {
    font-style: italic;
    color: #ddd;
    margin-bottom: 1.5rem;
    font-size: 1.1em;
}

/* --- Opponent Section in Detail --- */
.opponent-section {
    margin-top: 1.5rem;
    padding-top: 1.5rem;
    border-top: 1px dashed #ccc;
}

.opponent-title {
    margin-bottom: 0.5rem;
    font-size: 1.3em;
}

.opponent-img {
    max-width: 200px;
    height: auto;
    border-radius: 8px;
    border: 1px solid #eee;
    display: block;
}
